import React, { useEffect, useState } from "react";
import axios from "axios";
import './Main.css'
const Base=''
function Main() {
  const [Notes, setNotes] = useState([]);
  const [title, setTitle] = useState("");
  const [desc, setDesc] = useState("");
  function handleClick(e) {
    e.preventDefault()
    if(title==='' || desc===''){
      alert('fields must be filled')
      return;
    }
    axios
      .post("/add", { title: title, desc: desc })
      .then((res) => {
        console.log(res);
      });
  }
  function getNotes() {
    axios.get("/getall").then((res) => {
      setNotes(res.data);
    });
  }
  useEffect(() => {
    getNotes();
  }, []);

  function updatenote(Note) {
    const desc = prompt("new Desciption");
    console.log(desc);
    axios.put("/update", {
      id: Note._id,
      title: Note.title,
      desc: desc,
    });
    getNotes();
  }
  return (
    <div>
      <form>
        <table id="notes">
          <tbody>
            <tr>
              <td>
                <input
                  type="text"
                  placeholder="Title"
                  value={title}
                  onChange={(e) => setTitle(e.target.value)}
                />
              </td>
              <td>
                
                <textarea
                  placeholder="Write Something..."
                  value={desc}
                  onChange={(e) => setDesc(e.target.value)}
                />
              </td>
              <th>
                <button type="submit" onClick={handleClick}>
                  Add
                </button>
              </th>
            </tr>
          </tbody>
        </table>
      </form>
      <hr />
      <div className="notes">
        <table id="notes">
          <tbody>
            <tr>
              <th>title</th>
              <th>Description</th>
              <th>Update</th>
            </tr>
            {Notes.map((Note, index) => {
              return (
                <tr key={index}>
                  <td>{Note.title}</td>
                  <td> {Note.desc}</td>
                  <td>
                    <button onClick={() => updatenote(Note)}>update</button>
                  </td>
                </tr>
              );
            })}
          </tbody>
        </table>
      </div>
    </div>
  );
}

export default Main;
